import Vue from "vue";
import axios from "axios";
import config from "../dataServices/dataServiceConfig"
import store from '../store'

import fullData from "../mock/fullData.js";

import MockAdapter from "axios-mock-adapter";


var axiosInstance = axios.create({
  baseURL: process.env.VUE_APP_APIURL
});

var isFake = process.env.VUE_APP_FAKE == "true";
if (isFake) {
  var mock = new MockAdapter(axiosInstance, { delayResponse: 100 });

  for (const [key, value] of Object.entries(config)) {
    if (!fullData[key]) {
      console.error("data ve url emas uyumuyor key:" + key + " iin veri bulunamad.");
    }


    
    mock.onAny(value).reply((cfg) => {
     
      return [
        200,
        fullData[key] ? fullData[key] : { alert: true, message: "mock data bulunamad key:" + key }
      ]

    });
  }
}

axiosInstance.interceptors.request.use(
  request => {
    const token = sessionStorage.getItem("token");
    if (token) {
      request.headers['Authorization'] = 'Bearer ' + token;
    }

    return request;
  },
  error => {

    console.log(error);
    return Promise.reject(error);
  }
);

axiosInstance.interceptors.response.use(
  response => {

    if (response.data.alert) {
      alert(response.data.message);
    }
    return response;
  },
  error => {
    if (error.response && error.response.status === 401) {
      localStorage.removeItem('token');

    }
    if (error.response && error.response.status === 400 && error.response.data.message) {
      alert(error.response.data.message);
    }

    return Promise.reject(error);
  }
);

Vue.prototype.$axios = axiosInstance;

export default axiosInstance;

