<template >
  <div>
    <!-- <v-app-bar app src="https://picsum.photos/1920/1080?random"> </v-app-bar> -->
    <v-container style="max-width: 900px">
      <v-row>
        <v-col cols="12" md="12" sm="12">
          <v-alert
            border="left"
            colored-border
            color="grey"
            elevation="2"
            type="info"
            class="mb-0"
          >
            <h3 style="color: grey">
              Bu sayfa temsili bir sayfadr. Gerek randevu sisteminde
              bulunmayacaktr.
            </h3>
            <h4 style="color: grey">
              Test yapabilmek iin ayn anda hem bilgisayarda hem telefonda
              almaldr.
            </h4>
          </v-alert>
        </v-col>
      </v-row>
      <v-row justify="center">
        <v-col>
          <v-card class="mx-auto mb-0">
            <v-card-title class="mb-0"
              >QR kod ile temassz sra alma hakknda</v-card-title
            >
            <v-card-text>
              <h3>Bu sistemde iki farkl olaslk bulunmaktadr.</h3>
              <ol>
                <li>
                  <h4>Olaslk</h4>
                  <p>
                    Vatanda randevu aldnda kendisine SMS ile kiiye zel bir
                    balant gnderilir. Bu balantya tkladnda QR kod
                    okuyabilecei bir ekran alr. QR koddan vatandan
                    mdrle gittii tespit edilip sra almas salanr.
                  </p>
                </li>
                <li>
                  <h4>Olaslk</h4>
                  <p>
                    Vatanda mdrle gittiinde akll telefonundaki QR kod
                    okuyucu ile mdrlkteki hazrlanm olan kodu okutur. QR
                    koddan vatandan hangi mdrlkte olduu tespit edilir ama
                    kimlii tespit edilemez. Bunun iin kimlik numaras ve
                    birke ek bilgi istenerek kimlik tespiti yaplr. Eer
                    randevusu varsa sra almas salanr. Randevusu yoksa sra
                    almas iin randevusuz ilemler mensne ynlendirilir.
                  </p>
                </li>
              </ol>
            </v-card-text>
          </v-card>
        </v-col>
      </v-row>
      <v-row justify="center">
        <v-col>
          <v-card class="mx-auto mb-0">
            <v-card-title class="mb-0">Temsili SMS balants</v-card-title>
            <v-card-text>
              <h3>Bu balantya akll telefondan tklanmaldr.</h3>
              <h3>
                Balant aldktan sonra kamera eriimine izin verilmelidir.
              </h3>

              <div>
                <br />
                <ul>
                  <li v-for="(kisi, i) in getKisiList" :key="i">
                    <h2>TC:{{ kisi.tc }} Ad: {{ kisi.adi }}</h2>
                    <h4 v-if="kisi.randevu">(RANDEVULU)</h4>
                    <h4 v-if="!kisi.randevu">(RANDEVUSUZ)</h4>
                    <a v-if="kisi.randevu" :href="kisi.url.href">{{
                      kisi.url.text
                    }}</a>
                    <br />
                    {{ kisi.data }}
                    <br />
                    <br />
                  </li>
                </ul>
              </div>
              <br />
            </v-card-text>
          </v-card>
        </v-col>
      </v-row>
      <v-row justify="center">
        <v-col>
          <v-card class="mx-auto mb-0">
            <v-card-title class="mb-0">QR kod balants</v-card-title>
            <v-card-text>
              <h3>Bu balant QR okuyucudan elde edilen balantdr.</h3>

              <br />
              <div>
                <br />
                <ul>
                  <li v-for="(mudurluk, i) in getIlceList" :key="i">
                    <h2>{{ mudurluk.mudurluk.adi }}</h2>
                    <h3>{{ mudurluk.mudurluk.t }}</h3>
                    <a :href="mudurluk.url.href">{{ mudurluk.url.text }}</a>
                    <br />
                    {{ mudurluk.data }}
                    <br />
                    <v-btn
                      color="success"
                      elevation="4"
                      class="mr-5"
                      @click="createQR(mudurluk.url.text)"
                    >
                      QR KOD
                    </v-btn>
                    <br />
                    <br />
                  </li>
                </ul>
              </div>

              <!-- <a :href="getHref(mudurluk).href">{{ getHref(mudurluk).text }}</a> -->
            </v-card-text>
          </v-card>
        </v-col>
      </v-row>
    </v-container>
    <v-dialog v-model="dialog" width="350">
      <v-card class="mx-auto">
        <v-card-title class="mb-0">QR kodu okutabilirsiniz</v-card-title>
        <v-card-text>
          <canvas id="canvas" height="360"></canvas>
        </v-card-text>
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
import QRCode from "qrcode";
export default {
  data: () => ({
    dialog: false,
    mudurluk: {
      mid: 789,
    },
  }),
  methods: {
    getHref(base, data) {
      var link = {
        href: base + data,
        text:
          window.location.origin + process.env.VUE_APP_BASEURL + base + data,
      };
      return link;
    },
    createQR(link) {
      this.dialog = true;

      setTimeout(() => {
        QRCode.toCanvas(
          document.getElementById("canvas"),
          link,
          { width: 300 },
          (error) => {
            if (error) console.error(error);
          }
        );
      }, 100);
    },
  },
  computed: {
    getKisiList() {
      return this.$store.state.kisiListesi.map((k) => {
        var data = {
          adi: k.adi,
          tc: k.tc,
          randevu: k.randevu,
          url: this.getHref(
            "#/qr/",
            btoa(JSON.stringify({ tc: k.tc, typ: k.typ }))
          ),
          data: { tc: k.tc, typ: k.typ },
        };
        return data;
      });
    },
    getIlceList() {
      return this.$store.state.ilceListesi.map((k) => {
        var data = {
          mudurluk: k,
          url: this.getHref(
            "#/qr/",
            btoa(JSON.stringify({ id: k.id, h: k.h, t: k.t, typ: k.typ }))
            //k.typ+"|"+k.id+"|"+k.t.toJSON()+"|"+k.h
          ),
          data: { id: k.id, h: k.h, t: k.t, typ: k.typ },
        };
        return data;
      });
    },
  },
  mounted() {
    var kisiler = [
      {
        typ: "p",
        tc: 123,
        adi: "Hasan Kvlcm",
        randevu: true,
        mudurlukId: 12,
      },
      { typ: "p", tc: 456, adi: "Mehmet Ayba", randevu: true, mudurlukId: 12 },
      { typ: "p", tc: 789, adi: "Aye Trk", randevu: true, mudurlukId: 10 },
      { typ: "p", tc: 321, adi: "Mahmut Kvlcm", randevu: false },
      { typ: "p", tc: 654, adi: "Emine Sur", randevu: false },
      { typ: "p", tc: 987, adi: "Ayfer Afakan", randevu: false },
    ];
    this.$store.commit("setKisiListesi", { data: kisiler });
    var ilceler = [
      {
        typ: "m",
        id: 10,
        adi: "Keiborlu",
        gecerlimi: true,
        t: new Date(),
        h: "1234567890yuiopasqwertdfghjklzxc",
      },
      {
        typ: "m",
        id: 11,
        adi: "Muratpaa",
        gecerlimi: true,
        t: new Date(),
        h: "123456uiertyhjklzqwxcopasdfg7890",
      },
      {
        typ: "m",
        id: 12,
        adi: "ankaya",
        gecerlimi: true,
        t: new Date(),
        h: "123459tyuio0qwerjklzxcp678asdfgh",
      },
      {
        typ: "m",
        id: 13,
        adi: "Yenimahalle",
        gecerlimi: false,
        t: new Date("2021-05-20T03:24:00"),
        h: "123tyuiop45678xc9erasdf0qwghjklz",
      },
    ];
    this.$store.commit("setIlceListesi", { data: ilceler });
  },
};
</script>