<template >
  <div>
    <!-- <v-app-bar app src="https://picsum.photos/1920/1080?random"> </v-app-bar> -->
    <v-slide-y-transition mode="out-in">
      <v-container style="max-width: 900px" v-if="show">
        <v-row>
          <v-col cols="12" md="12" sm="12">
            <v-alert
              border="left"
              colored-border
              color="grey"
              elevation="2"
              type="info"
              class="mb-0"
            >
              <h3 style="color: grey" v-if="kisi">
                {{ kisi.ad }} {{kisi.soyad}} iin istenilen bilgileri giriniz.
              </h3>
              <!-- <h5 style="color: grey; text-align: center">
              Oluturduunuz randevular iin tarih ve saat seerek devam
              edebilirsiniz.
            </h5> -->
            </v-alert>
          </v-col>
        </v-row>
        <v-row justify="center">
          <v-col md="12">
            <v-card elevation="4" v-if="kisi">
              <v-card-title class="text-h5"> Hes Kodu </v-card-title>
              <v-card-text>
                <v-form>
                  <v-container>
                    <v-row>
                      <v-col cols="12" md="12" sm="12" class="pb-0 pt-0">
                        <v-checkbox
                          v-model="onlytc"
                          label="TC Kimlik Kartm veya Nfus Czdanm olmad iin / Yeni Vatanda olduum iin randevu alyorum."
                        ></v-checkbox>
                      </v-col>
                      <v-col cols="12" md="12" sm="12" class="pb-0 pt-0">
                        <v-checkbox
                          v-model="hesOnay"
                          label="Gerekli durumlarda HES kodumun biti sresinin uzatlmas ve blokelenmesine izin veriyorum."
                        ></v-checkbox>
                      </v-col>
                      <v-col cols="12" md="12" sm="12">
                        <v-text-field
                          label="Hes Kodu"
                          required
                          outlined
                          dense
                        ></v-text-field>
                      </v-col>
                    </v-row>
                  </v-container>
                </v-form>
              </v-card-text>
            </v-card>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12" md="12" sm="12" class="pt-1">
            <v-card elevation="4">
              <v-card-actions>
                <v-btn color="success" class="" @click="geri()"> GER </v-btn>
                <v-spacer></v-spacer>

                <v-btn color="success" class="" @click="ileri()"> LER </v-btn>
              </v-card-actions>
            </v-card>
          </v-col>
        </v-row>
      </v-container></v-slide-y-transition
    >
  </div>
</template>
<script>
export default {
  data: () => ({
    hesOnay: false,
    onlytc: false,
    show: true,
  }),
  methods: {
    
    ileri() {
      this.giziegoster();
      var index = parseInt(this.$route.params.index) + 1;

      if (index > this.$store.state.islemKisiList.length - 1) {
        this.$router.push({ name: "smsdogrula" });
      } else {
        if (this.$route.params.index !== index) {
          this.$router.push({ name: "kisiekbilgi", params: { index: index } });
        }
      }
    },
    giziegoster() {
      setTimeout(() => {
        this.show = false;
      }, 200);
      setTimeout(() => {
        this.show = true;
      }, 600);
    },
    geri() {
      this.giziegoster();
      this.$router.go(-1);
    },
  },
  mounted() {
    //var index = this.$route.params.index;
    //this.kisi = this.$store.state.islemKisiList[index];
  },
  computed: {
    kisi() {
      var index = parseInt(this.$route.params.index);
      var kisi = this.$store.state.islemKisiList[index];
      
      return kisi;
    },
  },
};
</script>