<template >
  <div>
    <!-- <v-app-bar app src="https://picsum.photos/1920/1080?random"> </v-app-bar> -->
    <v-container style="max-width: 900px">
      <v-row>
        <v-col cols="12" md="12" sm="12">
          <v-alert
            border="left"
            colored-border
            color="grey"
            elevation="2"
            type="info"
            class="mb-0"
          >
            <h3 style="color: grey" v-if="getMudurluk && randevuSayisi">
              {{ getMudurluk.aciklama }} nfus mdrl iin
              {{ randevuSayisi }} adet randevu oluturdunuz
            </h3>
            <h3 style="color: grey" v-if="getMudurluk && !randevuSayisi">
              {{ getMudurluk.aciklama }} nfus mdrl iin henz randevu
              oluturmadnz
            </h3>
            <h5 style="color: grey">
              Oluturduunuz randevular iin tarih ve saat seerek devam
              edebilirsiniz.
            </h5>
          </v-alert>
        </v-col>
      </v-row>
      <v-row justify="center">
        <v-col cols="12" md="4" sm="4">
          <v-card class="mx-auto mb-0">
            <v-list class="mb-0">
              <v-subheader>Adna ilem yapabileceiniz kiiler</v-subheader>

              <v-list-item-group color="primary" v-model="selectedPerson">
                <v-list-item
                  v-for="item in getKisiList"
                  :key="item.id"
                  @click="kisiSec(item)"
                >
                  <v-list-item-avatar>
                    <v-icon class="grey lighten-1" dark> mdi-account </v-icon>
                  </v-list-item-avatar>

                  <v-list-item-content>
                    <v-list-item-title
                      >{{ item.ad }} {{ item.soyad }}</v-list-item-title
                    >

                    <v-list-item-subtitle>{{
                      item.yakinlik
                    }}</v-list-item-subtitle>
                  </v-list-item-content>

                  <v-list-item-action>
                    <v-icon color="grey lighten-1" large
                      >mdi-chevron-right</v-icon
                    >
                  </v-list-item-action>
                </v-list-item>
              </v-list-item-group>
            </v-list>
          </v-card>
        </v-col>

        <v-col cols="12" md="8" sm="8">
          <v-alert
            v-if="types.length == 0"
            border="left"
            colored-border
            color="grey"
            elevation="2"
            type="info"
          >
            <h3 style="color: grey; text-align: center">
              Henz ekleme yapmadnz.
            </h3>
            <h5 style="color: grey; text-align: center">
              Kii ve ilem tipi seerek randevu oluturabilirsiniz.
            </h5>
          </v-alert>
          <v-card
            class="mx-auto mb-4"
            max-width="600"
            v-for="(item, i) in types"
            :key="i"
          >
            <div :ref="item.id">
              <v-card-title class="mb-0">
                <div style="width: 80%" @click="show(item)">
                  {{ item.person.ad }} {{ item.person.soyad }}
                </div>

                <v-spacer></v-spacer>
                <v-btn color="red" @click="sil(item)" icon> SL </v-btn>
              </v-card-title>

              <v-card-subtitle @click="show(item)" class="pb-1">
                {{ tipGoster(item.multiType) }}
              </v-card-subtitle>
              <div @click="show(item)">
                <v-card-subtitle class="pt-0 pb-0">
                  TARH:
                  <span v-if="item.seciliTarihSaat">{{
                    new Date(item.seciliTarihSaat)
                      .toLocaleString("tr-TR")
                      .substring(0, 16)
                  }}</span>
                  <span v-if="!item.seciliTarihSaat">Tarih Seilmedi</span>
                </v-card-subtitle>

                <v-card-actions class="pt-0">
                  <v-btn color="orange lighten-2" text> Saat se </v-btn>

                  <v-spacer></v-spacer>

                  <v-btn icon>
                    <v-icon>{{
                      showId == item.id ? "mdi-chevron-up" : "mdi-chevron-down"
                    }}</v-icon>
                  </v-btn>
                </v-card-actions>
              </div>
              <v-expand-transition>
                <div v-if="showId == item.id">
                  <v-divider></v-divider>

                  <v-row class="mt-1" justify="center" align="center">
                    <v-btn-toggle rounded class="mb-4 mt-2">
                      <v-btn
                        :disabled="!item.tarihList || item.tarihList.length < 1"
                        icon
                        elevation="2"
                        medium
                        @click="tarihDegistir(i, -1)"
                      >
                        <v-icon>mdi-chevron-left</v-icon>
                      </v-btn>
                      <v-btn
                        text
                        elevation="2"
                        medium
                        :disabled="!item.tarihList || item.tarihList.length < 1"
                      >
                        {{
                          item.tarihList && item.tarihList.length > 0
                            ? new Date(
                                item.tarihList[item.seciliTarihIndex].date
                              )
                                .toLocaleString("tr-TR")
                                .substring(0, 10)
                            : "Tarih Yok"
                        }}
                      </v-btn>
                      <v-btn
                        icon
                        :disabled="!item.tarihList || item.tarihList.length < 1"
                        elevation="2"
                        medium
                        @click="tarihDegistir(i, 1)"
                      >
                        <v-icon>mdi-chevron-right</v-icon>
                      </v-btn>
                    </v-btn-toggle>
                  </v-row>
                  <v-card-text>
                    <v-row
                      justify="center"
                      align="center"
                      style="max-height: 268px; overflow-y: scroll"
                    >
                      <v-col
                        v-for="(n, idx) in item.saatList"
                        :key="idx"
                        cols="auto"
                        class="pa-2"
                      >
                        <v-btn
                          :disabled="n.available < 1"
                          text
                          elevation="2"
                          height="50"
                          width="75"
                          @click="saatSec(i, n)"
                        >
                          {{ saatGoster(n.time) }}
                        </v-btn>
                      </v-col>
                    </v-row>
                  </v-card-text>
                </div>
              </v-expand-transition>
            </div>
          </v-card>
          <v-card elevation="4">
            <v-card-actions>
              <v-btn color="success" class="" @click="geri()"> GER </v-btn>
              <v-spacer></v-spacer>

              <v-btn color="success" class="" @click="ileri()"> LER </v-btn>
            </v-card-actions>
          </v-card>
        </v-col>
      </v-row>
    </v-container>
    <v-dialog v-model="dialog" max-width="360">
      <v-card class="mx-auto">
        <v-list>
          <v-subheader>Seilen mdrlkte yaplabilen ilemler</v-subheader>

          <v-list-item-group color="primary">
            <v-list-item
              v-for="item in uygunRandevular"
              :key="item.id"
              :disabled="item.rate == 100"
              @click="ekle(item)"
            >
              <v-list-item-avatar>
                <v-icon class="grey lighten-1" dark> mdi-tag </v-icon>
              </v-list-item-avatar>
              <v-list-item-content>
                <v-list-item-title>{{ item.typeName }}</v-list-item-title>

                <v-list-item-subtitle>{{
                  "Doluluk Oran %" + item.rate
                }}</v-list-item-subtitle>
              </v-list-item-content>
              <v-list-item-action>
                <v-list-item-action-text
                  >En yakn tarih</v-list-item-action-text
                >

                <v-list-item-action-text>{{}}</v-list-item-action-text>
              </v-list-item-action>
            </v-list-item>
          </v-list-item-group>
        </v-list>
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
import RandevuDataService from "../dataServices/randevuDataService.js";
export default {
  data: () => ({
    dialog: false,
    showId: null,
    selectedPerson: null,
    uygunRandevular: [],
  }),
  methods: {
    saatGoster(tarih) {
      var date = new Date(tarih).toLocaleString("tr-TR").substring(10, 16);

      return date;
    },
    async saatSec(index, n) {
      var selectedTypes = this.$store.state.selectedTypes;

      var item = selectedTypes[index];

      item.seciliTarihSaat = n.time;

      this.$store.commit("setSelectedTypes", { data: selectedTypes });
      // sunucuya gnderme ilemi yaplacak
      var request = {
        hash: item.person.hash,
        randevuZamani: n.time,
        islemTurleri: item.multiType.map((k) => k.typeId),
        CokluRandevuGrupId: item.typeGroupId,
      };
      var result = await RandevuDataService.kisiEkle(request);
     
    },
    async kisiSec(kisi) {
      this.dialog = true;
      var uygunRandevular = await RandevuDataService.uygunIslemTipiListesiGetir(
        this.$store.state.seciliMudurluk.id
      );
      this.uygunRandevular = uygunRandevular.data;
    },
    async tarihDegistir(index, gun) {
      var selectedTypes = this.$store.state.selectedTypes;

      var item = selectedTypes[index];

      var gunIndex = item.seciliTarihIndex + gun;
      if (gunIndex < 0) {
        gunIndex = 0;
      } else if (gunIndex > item.tarihList.length - 1) {
        gunIndex = item.tarihList.length - 1;
      }
      if (item.seciliTarihIndex != gunIndex) {
        item.seciliTarihIndex = gunIndex;
        item.seciliTarihSaat = null;
        var saatList = await this.getSaatList(
          item.multiType[0].typeId,
          this.$store.state.seciliMudurluk.id,
          item.tarihList[item.seciliTarihIndex].date
        );
        item.saatList = saatList;
        this.$store.commit("setSelectedTypes", { data: selectedTypes });
      }
    },
    ileri() {
      var kisiler = this.$store.state.selectedTypes.map((k) => k.person);

      var distinctKisiler = Array.from(new Set(kisiler.map((s) => s.hash))).map(
        (h) => {
          return kisiler.find((p) => p.hash == h);
        }
      );

      if (distinctKisiler.length > 0) {
        this.$store.commit("setIslemKisiList", { data: distinctKisiler });
        this.$store.commit("setSeciliKisiIndex", { data: 0 });

        this.$router.push({ name: "kisiekbilgi", params: { index: 0 } });
      }
    },
    async show(item) {
      //this.$refs[item.id].scrollIntoView({behavior: "smooth"});
      await this.tarihSaatYenile();
      setTimeout(() => {
        this.$refs[item.id][0].scrollIntoView({
          behavior: "smooth",
          block: "center",
        });
      }, 200);
      this.showId =
        this.showId == item.id ? (this.showId = null) : (this.showId = item.id);
      this.$refs[item.id];
    },
    async sil(item) {
      var kalanlar = this.$store.state.selectedTypes.filter(
        (k) => !(k.id === item.id && k.person.id === item.person.id)
      );
     
      var result = await RandevuDataService.kisiRandevuSil(
        item.person.hash,
        item.groupId
      );

      this.$store.commit("setSelectedTypes", { data: kalanlar });
    },
    async ekle(item) {
      if (this.selectedPerson != null && this.selectedPerson > -1) {
        var person = { ...this.$store.state.kisiListesi[this.selectedPerson] };

        var result = await RandevuDataService.kisiRandevuSil(
          person.hash,
          item.groupId
        );

        var selectedTypes = this.$store.state.selectedTypes;
        var selectedType = selectedTypes.find(
          (k) => k.person.hash == person.hash && k.typeGroupId == item.groupId
        );

        var tarihList = await this.getTarihList(
          this.$store.state.seciliMudurluk.id,
          item.typeId
        );

        if (tarihList.length > 0) {
          var saatList = await this.getSaatList(
            item.typeId,
            this.$store.state.seciliMudurluk.id,
            tarihList[0].date
          );
        }

        if (selectedType) {
          if (!selectedType.multiType.some((k) => k.typeId == item.typeId)) {
            selectedType.multiType.push(item);
            selectedType.tarihList = tarihList;
            selectedType.seciliTarihIndex = 0;
            selectedType.saatList = saatList;
          }
        } else {
          selectedType = {
            person: person,
            id: Math.floor(Math.random() * 100000),

            multiType: [item],
            typeGroupId: item.groupId,

            tarihList: tarihList,
            seciliTarihIndex: 0,
            saatList: saatList,
          };

          selectedTypes.push({ ...selectedType });
        }
        
        this.showId = selectedType.id;

        //await this.tarihSaatYenile();
        setTimeout(() => {
          this.$refs[this.showId][0].scrollIntoView({
            behavior: "smooth",
            block: "center",
          });
        }, 500);
        this.$store.commit("setSelectedTypes", { data: selectedTypes });
      }
      this.dialog = false;
      this.selectedPerson = null;
    },
    async tarihSaatYenile() {
      var selectedTypes = this.$store.state.selectedTypes;
      selectedTypes.forEach(async (item, index) => {
        
        if (item.multiType.length > 0) {
          var tarihList = await this.getTarihList(
            this.$store.state.seciliMudurluk.id,
            item.multiType[0].typeId
          );

          if (tarihList.length > 0) {
            var seciliTarih = item.tarihList[item.seciliTarihIndex];

            var saatList = await this.getSaatList(
              item.multiType[0].typeId,
              this.$store.state.seciliMudurluk.id,
              tarihList[0].date
            );
          }
        }
      });
    },
    async getTarihList(mudurlukId, typeId) {
      var tarihList = await RandevuDataService.tarihListesiGetir(
        mudurlukId,
        typeId
      );

      return tarihList.data;
    },
    async getSaatList(typeId, mudurlukId, tarih) {
      //return [];

      var result = await RandevuDataService.saatListesiGetir(
        mudurlukId,
        typeId,
        tarih
      );
      return result.data;
    },
    tipGoster(types) {
      return types ? types.map((k) => k.typeName).toString() : [].toString();
    },
    geri() {
      this.$router.go(-1);
    },
  },
  computed: {
    types() {
      return this.$store.state.selectedTypes;
    },
    getKisiList() {
      return this.$store.state.kisiListesi;
    },
    getMudurluk() {
      return this.$store.state.seciliMudurluk;
    },
    randevuSayisi() {
      return this.$store.state.selectedTypes.length;
    },
  },
  async mounted() {
    var token = sessionStorage.getItem("token");
    if (!token) {
      this.$router.push({
        name: "kimlikdogrula",
        params: { returnpage: "randevual" },
      });
    }
    //seciliMudurlukGetir
    var seciliMudurluk = await RandevuDataService.seciliMudurlukGetir();

    this.$store.commit("setMudurluk", { data: seciliMudurluk.data });
    var kisiList = await RandevuDataService.kisiListesiGetir();

    this.$store.commit("setKisiListesi", { data: kisiList.data });
  },
};
</script>