import Vue from 'vue'
import Vuex from 'vuex'


Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    selectedTypes: [],
    kisiListesi:[],
    ilceListesi:[],
    islemKisiList:[],
    seciliKisiIndex:null,
    seciliMudurluk:null,
    tarihListesi:[]
  },
  mutations: {
    setSelectedTypes(state, payload) {
      state.selectedTypes = [... payload.data];
    },
    setKisiListesi(state, payload) {
      state.kisiListesi = [... payload.data];
    },
    setIslemKisiList(state, payload) {
      state.islemKisiList = [... payload.data];
    },
    setSeciliKisiIndex(state, payload) {
      state.seciliKisiIndex =  payload.data;
    },
    setIlceListesi(state, payload) {
      state.ilceListesi = [... payload.data];
    },
    setMudurluk(state, payload) {
      state.seciliMudurluk = {... payload.data};
    },
    setTarihList(state, payload) {
      state.tarihListesi = [... payload.data];
    },
  },
  actions: {
  },
  modules: {
  }
})
