<template >
  <div>
    <v-container style="max-width: 900px">
      <v-row>
        <v-col cols="12" md="12" sm="12">
          <v-alert
            border="left"
            colored-border
            color="grey"
            elevation="2"
            type="info"
            class="mb-0"
          >
            <h3 style="color: grey">
              Kimliinizi dorulayarak ileme devam edebilirsiniz.
            </h3>
            <h5 style="color: grey">
              Kimlik dorulamas iin istenen kiisel bilgileri giriniz.
            </h5>
          </v-alert>
        </v-col>
      </v-row>
      <v-row justify="center">
        <v-col md="12">
          <v-card elevation="4">
            <v-card-title class="text-h5"> Kimlik dorulaynz </v-card-title>
            <v-card-text>
              <v-form v-model="valid">
                <v-container>
                  <v-row>
                    <v-col cols="12" md="6" sm="6">
                      <v-text-field
                        v-model="kisi.ad"
                       
                        label="Ad"
                        required
                        outlined
                        dense
                      ></v-text-field>
                    </v-col>

                    <v-col cols="12" md="6" sm="6">
                      <v-text-field
                        v-model="kisi.soyad"
                        
                        label="Soyad"
                        required
                        outlined
                        dense
                      ></v-text-field>
                    </v-col>

                    <v-col cols="12" md="6" sm="6">
                      <v-text-field
                        v-model="kisi.tcKimlikNo"
                        label="Kimlik No"
                        :counter="11"
                        required
                        outlined
                        dense
                      ></v-text-field>
                    </v-col>

                    <v-col cols="12" md="6" sm="6">
                      <v-text-field
                        v-model="kisi.telefonNo"
                        :counter="10"
                        label="Telefon (5XXXXXXXXX)"
                        required
                        outlined
                        dense
                      ></v-text-field>
                    </v-col>

                    <v-col cols="12" md="6" sm="6">
                      <v-menu
                        ref="menu"
                        v-model="menu"
                        :close-on-content-click="false"
                        :return-value.sync="kisi.dogumTarihi"
                        transition="scale-transition"
                        offset-y
                        min-width="auto"
                      >
                        <template v-slot:activator="{ on, attrs }">
                          <v-text-field
                            v-model="kisi.dogumTarihi"
                            label="Doum Tarihi"
                            readonly
                            outlined
                            dense
                            v-bind="attrs"
                            v-on="on"
                          ></v-text-field>
                        </template>
                        <v-date-picker
                          v-model="date"
                          no-title
                          scrollable
                          locale="tr-tr"
                        >
                          <v-spacer></v-spacer>
                          <v-btn text color="primary" @click="menu = false">
                            ptal
                          </v-btn>
                          <v-btn
                            text
                            color="primary"
                            @click="$refs.menu.save(date)"
                          >
                            Tamam
                          </v-btn>
                        </v-date-picker>
                      </v-menu>
                    </v-col>
                    <v-col cols="6" md="3" sm="3">
                      <v-img height="40" src="img/dogrulamakodu.png"></v-img>
                    </v-col>
                    <v-col cols="6" md="3" sm="3">
                      <v-text-field
                        v-model="kisi.dogrulamaKodu"
                        label="Dorulama Kodu"
                        :counter="6"
                        required
                        outlined
                        dense
                      ></v-text-field>
                    </v-col>
                  </v-row>
                </v-container>
              </v-form>
            </v-card-text>
          </v-card>
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="12" md="12" sm="12" class="pt-1">
          <v-card elevation="4">
            <v-card-actions>
              <v-btn color="success" class="" @click="geri"> GER </v-btn>
              <v-spacer></v-spacer>

              <v-btn color="success" class="" @click="giris"> LER </v-btn>
            </v-card-actions>
          </v-card>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>
<script>
import RandevuDataService from "../dataServices/randevuDataService.js";
export default {
  data: () => ({
    list: ["Randevu Al", "Eski Randevular Grntle"],
    kisi: {},
    valid: false,
    date: null,
    firstname: "",
    lastname: "",
    menu: false,
    // nameRules: [
    //   (v) => !!v || "Name is required",
    //   (v) => v.length <= 10 || "Name must be less than 10 characters",
    // ],
    email: "",
    // emailRules: [
    //   (v) => !!v || "E-mail is required",
    //   (v) => /.+@.+/.test(v) || "E-mail must be valid",
    // ],
  }),
  methods: {
    async giris() {
      this.kisi.mudurlukId= this.$store.state.seciliMudurluk.id
      var token = await RandevuDataService.kimlikDogrula(this.kisi);
     
      sessionStorage.setItem("token", token.token);
      this.$router.push({ path: "../" + this.$route.params.returnpage });
    },
    geri() {
      this.$router.go(-2);
    },
  },
  mounted() {},
};
</script>