<template >
  <div>
    <!-- <v-app-bar app src="https://picsum.photos/1920/1080?random"> </v-app-bar> -->

    <v-container style="max-width: 900px">
      <v-row>
        <v-col cols="12" md="12" sm="12">
          <v-alert
            border="left"
            colored-border
            color="grey"
            elevation="2"
            type="info"
            class="mb-0"
          >
            <h3 style="color: grey; text-align: center">SMS Dorulamas</h3>
            <h5 style="color: grey; text-align: center">
              Telefonunuza dorulama kodu gnderilmitir.
            </h5>
          </v-alert>
        </v-col>
      </v-row>
      <v-row justify="center">
        <v-col md="12">
          <v-card elevation="4">
            <v-card-title class="text-h5">
              Telefonunuza gnderilen kodu giriniz.
            </v-card-title>
            <v-card-text>
              <v-form>
                <v-container>
                  <v-row>
                    <v-col cols="12" md="12" sm="12" class="pb-0">
                      <v-text-field
                        v-model="code"
                        :counter="6"
                        label="SMS Kodu"
                        required
                        outlined
                        dense
                      ></v-text-field>
                    </v-col>
                    <v-col cols="12" md="6" sm="6" class="pt-0">
                      <v-btn :disabled="this.sure>0" color="success" @click="smsKoduOlustur()" >
                        TEKRAR GNDER ({{ sure }})</v-btn
                      >
                    </v-col>
                    <v-col cols="12" md="6" sm="6" class="pt-0"> </v-col>
                  </v-row>
                </v-container>
              </v-form>
            </v-card-text>
          </v-card>
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="12" md="12" sm="12" class="pt-1">
          <v-card elevation="4">
            <v-card-actions>
              <v-btn color="success" class="" @click="geri"> GER </v-btn>
              <v-spacer></v-spacer>

              <v-btn color="success" class="" @click="ileri"> LER </v-btn>
            </v-card-actions>
          </v-card>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>
<script>
import RandevuDataService from "../dataServices/randevuDataService.js";
export default {
  data: () => ({
    code: "",
    sure: 120,
  }),
  methods: {
    async ileri() {
      var result = await RandevuDataService.smsDogrula({ code: this.code });
      console.log(result)
      if (result.status) {
        this.$router.push({ name: "sonuc" });
      }
    },
    geri() {
      this.$router.go(-1);
    },
    async smsKoduOlustur() {
      var result = await RandevuDataService.smsKoduOlustur();
      console.log(result);
      this.sure=120;
      this.sayac= setInterval(() => {
        this.sure--;
        if (this.sure<1) {
          clearInterval(this.sayac);
        }
      }, 1000);
    },
  },
  async mounted() {
    await this.smsKoduOlustur();
  },
};
</script>