</template>
<script>
import RandevuDataService from "../dataServices/randevuDataService.js";
export default {
  computed: {
    types() {
      return this.$store.state.selectedTypes;
    },
    filteredIl() {
      if (!this.searchModel) {
        return this.ilList;
      }
      return this.ilList.filter((p) =>
        p.adi.includes(this.searchModel.toUpperCase())
      );
    },
    filteredIlce() {
      if (!this.selectedIl) {
        return [];
      } else {
        return this.ilceList; //.filter((p) => p.il_id == this.selectedIl);
      }
    },
  },
  data: () => ({
    selectedTypeIndex: [],
    selectedTypes: [],
    searchModel: "",
    searchModelilce: "",
    selectedItem: null,
    selectedIl: null,
    selectedIlce: null,

    uygunRandevular: [],
    ilList: [],
    ilceList: [],
  }),

  methods: {
    async ilcesec() {
      var mudurluk = this.filteredIlce[this.selectedIlce];
      this.$store.commit("setMudurluk", { data: mudurluk });

      //var result=await RandevuDataService.mudurlukSec(mudurluk);

      var uygunRandevular = await RandevuDataService.uygunIslemTipiListesiGetir(
        mudurluk.id
      );

      this.uygunRandevular = uygunRandevular.data;

      setTimeout(() => {
        this.$refs.info.scrollIntoView({
          behavior: "smooth",
          block: "center",
        });
      }, 100);
      var token = sessionStorage.getItem("token");
      if (token) {
        var result=await RandevuDataService.mudurlukSec(mudurluk);
      }
    },
    sec() {
      this.$router.push({ name: "randevusec" });
    },
    search(searchModel) {},
    async ilsec(il) {
      if (il) {
        var data = await RandevuDataService.ilceListesiGetir(il);
        this.ilceList = data.data;
      }
    },

    geri() {
      this.$router.go(-1);
    },
  },
  async mounted() {
    var data = await RandevuDataService.ilListesiGetir();
    this.ilList = data.data;
  },
};
</script>